@import url("https://fonts.googleapis.com/css2?family=Alata&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300&display=swap");
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: "Alata", sans-serif;
  font-size: 15px;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  display: inline-block;
  color: hsl(0deg, 0%, 100%);
}

body {
  min-height: 100vh;
  line-height: 1.5;
  text-rendering: optimizeSpeed;
}

.main-container {
  max-width: 1440px;
  margin: auto;
  background: hsl(0deg, 0%, 100%);
}

/* --- 01 TYPOGRAPHY SYSTEM
- Font sizes (px)
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98 
--- */
h1,
h2,
h3 {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 300;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 120px;
}

nav ul {
  display: grid;
  grid-template-columns: repeat(5, minmax(min-content, min-content));
  grid-gap: 32px;
}

nav ul li {
  position: relative;
}

nav ul li::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  width: 0;
  height: 2px;
  transform: translate(-50%);
  background: hsl(0deg, 0%, 100%);
}

nav ul li:hover::after {
  width: 25px;
}

.mobile-nav-brand {
  display: none;
  cursor: pointer;
}

.nav-toggler {
  cursor: pointer;
  display: none;
}

@media screen and (max-width: 992px) {
  .mobile-nav-brand {
    display: flex;
    position: absolute;
    right: 25px;
    left: 25px;
    top: 40px;
    justify-content: space-between;
    align-items: center;
  }
  .mobile-nav-brand img {
    max-width: 145px;
  }
  .navbar-nav {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100vh;
    background: hsl(0deg, 0%, 0%);
    padding: 25px;
    display: flex;
    align-items: center;
    z-index: 10;
    transition: 0.25s;
  }
  .navbar-nav.active {
    left: 0;
  }
  .navbar-nav ul {
    grid-template-columns: 1fr;
    grid-gap: 25px;
  }
  .navbar-nav ul li a {
    font-family: "Josefin Sans", sans-serif;
    font-size: 25px;
    font-weight: 300;
    text-transform: uppercase;
  }
  nav ul li::after {
    display: none;
  }
  .nav-toggler {
    display: block;
  }
}
@media screen and (max-width: 576px) {
  .navbar {
    margin-bottom: 160px;
  }
  .navbar-brand img {
    max-width: 145px;
  }
}
.header {
  background: rgba(0, 0, 0, 0.4) url(/images/desktop/image-hero.jpg) no-repeat center;
  background-size: cover;
  background-blend-mode: multiply;
  padding: 65px 165px;
  margin-bottom: 180px;
}

@media screen and (max-width: 1200px) {
  .header {
    padding: 65px 80px;
  }
}
@media screen and (max-width: 768px) {
  .header {
    padding: 65px 40px;
  }
}
@media screen and (max-width: 576px) {
  .header {
    padding: 40px 25px;
    background: url(/images/mobile/image-hero.jpg) center no-repeat;
    background-size: cover;
  }
}
footer {
  background: hsl(0deg, 0%, 0%);
  padding: 45px 165px;
  display: flex;
  justify-content: space-between;
}

.footer-brand {
  margin-bottom: 20px;
}

.footer-brand img {
  max-width: 140px;
}

.footer-right {
  text-align: right;
}

.social-links {
  margin-bottom: 20px;
}

.social-links a {
  font-size: 1.8em;
  color: hsl(0deg, 0%, 100%);
}

.social-links a:not(:last-child) {
  margin-right: 12px;
}

.copyright {
  color: hsl(0deg, 0%, 55%);
}

@media screen and (max-width: 1200px) {
  footer {
    padding: 45px 80px;
  }
}
@media screen and (max-width: 992px) {
  footer {
    flex-direction: column;
  }
  .footer-brand {
    text-align: center;
  }
  .footer-left {
    margin-bottom: 30px;
  }
  .footer-nav ul {
    place-content: center;
  }
  .footer-right {
    text-align: center;
  }
}
@media screen and (max-width: 768px) {
  footer {
    padding: 55px 40px;
  }
  .footer-left {
    margin-bottom: 50px;
  }
  .footer-brand {
    margin-bottom: 30px;
  }
}
@media screen and (max-width: 576px) {
  footer {
    padding: 55px 25px;
  }
  .footer-nav ul {
    grid-template-columns: 1fr;
    grid-gap: 20px;
    text-align: center;
  }
  .social-links {
    margin-bottom: 15px;
  }
}
.about {
  position: relative;
  padding: 0 165px;
  margin-bottom: 180px;
}

.about .img-box img {
  display: block;
}

.about .content-box {
  position: absolute;
  top: 180px;
  left: 50%;
  padding: 95px;
  max-width: 640px;
  background: hsl(0deg, 0%, 100%);
}

.about .content-box h2 {
  font-size: 3.2em;
  margin-bottom: 30px;
}

.about .content-box p {
  color: hsl(0deg, 0%, 55%);
  line-height: 1.7;
}

@media screen and (max-width: 1200px) {
  .about {
    padding: 0 80px;
  }
  .about .img-box {
    margin-bottom: 80px;
  }
  .about .img-box img {
    width: 100%;
  }
  .about .content-box {
    position: static;
    max-width: 100%;
    text-align: center;
    padding: 95px;
  }
}
@media screen and (max-width: 768px) {
  .about {
    padding: 0 40px;
    margin-bottom: 120px;
  }
  .about .content-box {
    padding: 50px;
  }
  .about .content-box h2 {
    font-size: 2.8em;
  }
}
@media screen and (max-width: 576px) {
  .about {
    padding: 0 25px;
    margin-bottom: 100px;
  }
  .about .content-box h2 {
    font-size: 2.1em;
    margin-bottom: 20px;
  }
  .about .img-box {
    margin-bottom: 50px;
  }
  .about .content-box {
    padding: 15px;
  }
}
.banner h1 {
  max-width: 650px;
  padding: 38px;
  margin-bottom: 80px;
  border: 3px solid hsl(0deg, 0%, 100%);
  font-size: 4.7em;
  color: hsl(0deg, 0%, 100%);
}

@media screen and (max-width: 768px) {
  .banner h1 {
    padding: 22px;
    font-size: 4em;
  }
}
@media screen and (max-width: 576px) {
  .banner h1 {
    font-size: 2.6em;
    margin-bottom: 175px;
  }
}
.product {
  position: relative;
  padding: 0 165px;
  margin-bottom: 180px;
}

.product h2 {
  font-size: 3.2em;
  margin-bottom: 80px;
}

/**
 * product grid style
 */
.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  place-content: center;
  grid-gap: 30px;
}

.product-card {
  position: relative;
  height: 450px;
  cursor: pointer;
}

.product-card img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: top;
}

.product-card::after {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(180deg, transparent 40%, rgba(0, 0, 0, 0.7333333333));
}

.product-card:hover::after {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.6666666667) 40%, rgba(85, 85, 85, 0.6666666667));
}

.product-card h3 {
  position: absolute;
  bottom: 35px;
  left: 40px;
  font-size: 2.1em;
  color: hsl(0deg, 0%, 100%);
  z-index: 5;
}

.product-card:hover h3 {
  color: hsl(0deg, 0%, 0%);
}

.btn-primary {
  position: absolute;
  right: 165px;
  top: 5px;
  padding: 8px 35px;
  letter-spacing: 5px;
  border: 1px solid hsl(0deg, 0%, 0%);
  color: hsl(0deg, 0%, 0%);
}

.btn-primary:hover {
  color: hsl(0deg, 0%, 100%);
  background: hsl(0deg, 0%, 0%);
}

@media screen and (max-width: 1200px) {
  .product {
    padding: 0px 80px;
  }
}
@media screen and (max-width: 992px) {
  .product h2 {
    text-align: center;
  }
  .btn-primary {
    position: static;
    display: block;
    width: max-content;
    margin: auto;
    margin-top: 80px;
  }
}
@media screen and (max-width: 768px) {
  .product {
    padding: 0 40px;
    margin-bottom: 120px;
  }
  .product h2 {
    font-size: 2.8em;
  }
}
@media screen and (max-width: 576px) {
  .product {
    padding: 0 25px;
    margin-bottom: 100px;
  }
  .product h2 {
    font-size: 2.1em;
    margin-bottom: 20px;
  }
  .product-grid {
    grid-gap: 25px;
  }
  .product-card {
    height: 117px;
  }
  .product-card h3 {
    bottom: 15px;
    left: 20px;
  }
  .btn-primary {
    margin-top: 35px;
  }
}